@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.background{
  height: 350px;
  background-image: url(/spa/static/media/hero2.29dd7655.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  position: relative;
  border-bottom: 60px solid white;
}


.header--container {
  height: 60px;
}
.header--spa {
  width: 450px;
  object-fit: cover;
  height: 110px;
  border-radius: 10px;
}
.header--links-nav {
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
  width: 100%;
  height: 50px;
  background-color: rgb(255, 255, 255)
}
.header--links-ul {
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header--links-li {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25%;
  list-style: none;
  text-align: center;
  transition: 0.7s;
}

.header--links-li:hover {
  transition: 0.7s;
}
.header--button {
  font-family: "Yusei Magic", sans-serif;
  font-size: 18px;
  width: 100%;
  text-decoration: none;
  color: black;
  outline: none;
  border: none;
  background-color:rgb(255, 255, 255);
  transition: 0.7s;
}
 
.header--button:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
  transition: 0.7s;

}

.footer--container {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px 0 10px;
  }
  .footer--container-cards {
    display: flex;
    justify-content: space-between;
    width: 150px;
  }
  .footer--text {
    font-size: 16px;
  }
  .footer--cards {
    width: 32px;
    height: 22px;
    object-fit: cover;
  }
.container--wrapper{
    width:  1366px;
    margin: 0 auto;
}
.image-container{
  height: 520px;
}
.image-container img{
  height: 100%;
  width: 60%;
  object-fit: contain;

}
.each-fade > div img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}



